import React from 'react';
import styled from 'styled-components';
import { surfaceLiquid01, surfaceLiquid02 } from '@sberdevices/plasma-tokens';
import { Skeleton } from 'components/Skeleton/Skeleton';
import { layout } from 'style/const/layout@portal';

import { cardGapPx } from '../List/CatalogList';

const catalogCardGap = 32;
const catalogCardHeight = 506;
const catalogCardWidth = 330;
const catalogContentPaddingY = 32;
const catalogListScrollSpeed = 400;

const columnCount = Math.floor((layout.width + cardGapPx) / (catalogCardWidth + cardGapPx)) + 1;

const getWidthByCount = (count: number) => catalogCardWidth * count + cardGapPx * (count - 1);

const skeletonWidth = getWidthByCount(columnCount);

const skeletonPaddingX = (layout.width - getWidthByCount(columnCount - 1)) / 2;
const skeletonPaddingY = (layout.contentHeight - catalogCardWidth) / 2 - 75;

const backgroundColor = surfaceLiquid01;
const foregroundColor = surfaceLiquid02;

const StyledRoot = styled.div`
    box-sizing: border-box;
    height: ${layout.contentHeight}px;
    padding-left: ${skeletonPaddingX}px;
    padding-top: ${skeletonPaddingY}px;
`;

export const CatalogSkeletonPortal: React.FC = () => (
    <StyledRoot>
        <Skeleton
            width={skeletonWidth}
            height={catalogCardHeight}
            backgroundColor={backgroundColor}
            foregroundColor={foregroundColor}
            itemWidth={catalogCardWidth}
            itemHeight={catalogCardHeight}
            borderRadius={40}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={cardGapPx}
            paddingBetweenColumns={cardGapPx}
            speed={2}
        />
    </StyledRoot>
);
