import React from 'react';
import styled from 'styled-components';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { closeAppAction } from 'store/actions/app';
import {
    DialogPageSberbox,
    DialogTitleSberbox,
    DialogButtonSberbox,
    DialogTextSberbox,
} from 'components/DialogPage/DialogPage@sberbox';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';

import { ReactComponent as CirclesSvg } from './Error.assets/circles.svg';
import { ReactComponent as WarningSvg } from './Error.assets/warning.svg';

const StyledContainer = styled.div`
    position: absolute;
    top: -280px;
    right: -266px;

    display: flex;
    justify-content: center;
    align-items: center;
`;

const Circles = styled(CirclesSvg)`
    box-sizing: border-box;
    width: 1596px;
    height: 1596px;
`;

const WarningIcon = styled(WarningSvg)`
    position: absolute;
    top: 50%;
    left: 50%;

    width: 384px;
    height: 384px;

    transform: translate(-50%, -50%);
`;

export const Error: React.FC = () => {
    const { title, subtitle, retryButtonText, closeButtonText } = useSelector(({ errorPage }) => errorPage);

    const dispatch = useDispatch();

    const buttons = React.useMemo(
        () => (
            <>
                {retryButtonText ? (
                    <DialogButtonSberbox
                        shouldFocusOnMount
                        onClick={() => {
                            dispatch(goBack());
                        }}
                    >
                        {retryButtonText}
                    </DialogButtonSberbox>
                ) : null}
                {closeButtonText ? (
                    <DialogButtonSberbox
                        onClick={() => {
                            dispatch(closeAppAction());
                        }}
                    >
                        {closeButtonText}
                    </DialogButtonSberbox>
                ) : null}
            </>
        ),
        [retryButtonText, closeButtonText, dispatch],
    );

    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <StyledContainer>
                    <Circles />
                    <WarningIcon />
                </StyledContainer>
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPageSberbox buttons={buttons} decorations={decorations}>
            <DialogTitleSberbox>
                <MultilineText text={title} />
            </DialogTitleSberbox>
            <DialogTextSberbox>
                <MultilineText text={subtitle} />
            </DialogTextSberbox>
        </DialogPageSberbox>
    );
};
