import React from 'react';
import { layout } from 'style/const/layout@portal';

import { HeaderBackButtonPortal } from './BackButton/HeaderBackButton@portal';
import { HeaderCommon } from './Header';

const headerPadding = `52px 88px 0 ${layout.headerPaddingHorizontal}px`;

export const HeaderPortal: React.FC = () => {
    const renderBackButton = React.useCallback(() => <HeaderBackButtonPortal />, []);

    return <HeaderCommon headerPadding={headerPadding} layout={layout} renderBackButton={renderBackButton} />;
};
