import React from 'react';
import styled from 'styled-components';
import { Body1, Body2 } from '@sberdevices/plasma-styles';
import { accent, primary, warning } from '@sberdevices/plasma-tokens';
import { useSelector } from 'hooks/useSelector';

import { CartListItemTouch } from '../ListItem/CartListItem@touch';

const StyledCartListTouch = styled.div`
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;

    margin-top: 17px;
`;

const StyledContainer = styled.div`
    height: 190px;
    margin-bottom: 24px;
`;

const StyledTotalPrice = styled(Body2)`
    padding-left: 16px;

    color: ${primary};
`;

const StyledPrice = styled.span`
    color: ${accent};
`;

const StyledMinimalPrice = styled(Body1)`
    margin-top: 8px;

    padding-left: 16px;

    color: ${warning};
`;

export const CartListTouch: React.FC = () => {
    const { order, totalPrice, toMinimumPrice } = useSelector(({ cartPageTouch }) => cartPageTouch);

    return (
        <StyledCartListTouch>
            {order.length > 0 && (
                <StyledContainer>
                    {order.map((itemId, index) => (
                        <CartListItemTouch key={itemId} id={itemId} index={index} />
                    ))}
                </StyledContainer>
            )}
            <StyledTotalPrice>
                Сумма к оплате
                <StyledPrice> {totalPrice} ₽</StyledPrice>
            </StyledTotalPrice>
            {Boolean(toMinimumPrice) && (
                <StyledMinimalPrice>До минимального заказа еще {toMinimumPrice} ₽</StyledMinimalPrice>
            )}
        </StyledCartListTouch>
    );
};
