import React from 'react';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { DialogPagePortal, DialogTitlePortal } from 'components/DialogPage/DialogPage@portal';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';
import { layout } from 'style/const/layout@portal';

import { WaitAnimation } from './WaitAnimation';

export const WaitPortal: React.FC = () => {
    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <WaitAnimation layout={layout} />
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPagePortal decorations={decorations}>
            <DialogTitlePortal>
                <MultilineText text={'Оформляем\n заказ'} />
            </DialogTitlePortal>
        </DialogPagePortal>
    );
};
