import React from 'react';
import styled from 'styled-components';
import { Body1 } from '@sberdevices/plasma-styles';
import { PlasmaIcon } from 'components/PlasmaIcon/PlasmaIcon';
import { Input } from 'components/Input/Input@touch';
import { Button, ButtonViews } from 'components/Button/Button';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import {
    addressFullApartmentInputChangeAction,
    addressFullDoorCodeInputChangeAction,
    addressFullEntranceInputChangeAction,
    addressFullFloorInputChangeAction,
    addressFullPhoneNumberInputChangeAction,
} from 'store/actions/addressFullPage';
import { addressFullSubmitAction } from 'store/actions/server';
import { noop } from 'tools/noop';

const StyledAddressFullAllTouch = styled.div`
    overflow-y: auto;
    box-sizing: border-box;
    display: grid;
    grid-row-gap: 16px;
    grid-column-gap: 32px;
    padding: 14px 16px 0;
`;

const StyledVoiceHint = styled(Body1)`
    display: flex;
    align-items: center;
    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;
    padding-bottom: 16px;
`;

const StyledPlasmaIcon = styled(PlasmaIcon)`
    height: 64px;
    width: 72px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.03);
    box-shadow: inset 0 2px 2px rgba(255, 255, 255, 0.15);
    border-radius: 100%;
    margin-right: 16px;
    padding: 16px;
`;

const Row = styled.div`
    display: grid;
`;

const PaddingRow = styled(Row)`
    padding-bottom: 16px;
`;

const TwoRow = styled(Row)`
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 8px;
`;

const SubmitButton = styled(Button)`
    flex-grow: 0;
    padding: 4px 0;
    width: 143px;
    font-size: 16px;
    border-radius: 16px;

    &[disabled] {
        opacity: 0.3;
    }
`;

export const AddressFullAllTouch: React.FC = () => {
    const dispatch = useDispatch();
    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        isPhoneValid,
    } = useSelector(({ addressFullPage }) => addressFullPage);

    const handleApartmentInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullApartmentInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    const handleEntranceInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullEntranceInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    const handleFloorInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullFloorInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    const handleDoorCodeInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullDoorCodeInputChangeAction(e.target.value));
        },
        [dispatch],
    );

    const handlePhoneInputChange = React.useCallback(
        ({ nativeEvent }: React.ChangeEvent<HTMLInputElement>) => {
            if (nativeEvent instanceof InputEvent) {
                const { inputType, data } = nativeEvent;

                dispatch(addressFullPhoneNumberInputChangeAction({ inputType, data }));
            }
        },
        [dispatch],
    );

    const handleSubmitClick = React.useCallback(() => {
        dispatch(addressFullSubmitAction());
    }, [dispatch]);

    return (
        <StyledAddressFullAllTouch>
            <StyledVoiceHint>
                <StyledPlasmaIcon fill="url(#plasma-green-gradient)">
                    <linearGradient id="plasma-green-gradient">
                        <stop offset="0%" stopColor="#00FFE6" />
                        <stop offset="100%" stopColor="#80FF00" />
                    </linearGradient>
                </StyledPlasmaIcon>
                Вы можете заполнять данные голосом
            </StyledVoiceHint>
            <PaddingRow>
                <Input title="Улица и дом" value={addressInputValue} onChange={noop} />
            </PaddingRow>
            <Row>
                <Input title="Квартира" value={apartmentInputValue} onChange={handleApartmentInputChange} />
            </Row>
            <TwoRow>
                <Input title="Подъезд" value={entranceInputValue} onChange={handleEntranceInputChange} />
                <Input title="Этаж" value={floorInputValue} onChange={handleFloorInputChange} />
            </TwoRow>
            <PaddingRow>
                <Input title="Комментарий" value={doorCodeInputValue} onChange={handleDoorCodeInputChange} />
            </PaddingRow>
            <PaddingRow>
                <Input
                    title="Номер телефона"
                    value={phoneNumberInputValue}
                    onChange={handlePhoneInputChange}
                    isInvalid={!isPhoneValid}
                />
            </PaddingRow>
            <Row>
                <SubmitButton
                    view={ButtonViews.primary}
                    shouldLockAfterClick
                    onClick={handleSubmitClick}
                    disabled={isSubmitButtonDisabled}
                >
                    Сохранить
                </SubmitButton>
            </Row>
        </StyledAddressFullAllTouch>
    );
};
