import React from 'react';
import styled from 'styled-components';
import { Body1 } from '@sberdevices/plasma-styles';
import { primary } from '@sberdevices/plasma-tokens';
import { Spinner } from 'components/Spinner/Spinner';
import { MultilineText } from 'components/MultilineText/MultilineText';

const StyledContainer = styled.div`
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
`;

const StyledText = styled(Body1)`
    margin-top: 32px;

    color: ${primary};
`;

interface SpinnerPageProps {
    text?: string;
}

export const SpinnerPage: React.FC<SpinnerPageProps> = ({ text = '' }) => (
    <StyledContainer>
        <Spinner />
        {text && (
            <StyledText>
                <MultilineText text={text} />
            </StyledText>
        )}
    </StyledContainer>
);
