import React from 'react';
import { ButtonViews } from 'components/Button/Button';
import { MultilineText } from 'components/MultilineText/MultilineText';
import {
    DialogButtonSberbox,
    DialogPageSberbox,
    DialogTextSberbox,
    DialogTitleSberbox,
} from 'components/DialogPage/DialogPage@sberbox';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';
import { layout } from 'style/const/layout@sberbox';

import { useSuccessPage } from './Success.hooks';
import { SuccessAnimation } from './SuccessAnimation';

export const SuccessSberbox: React.FC = () => {
    const { deliveryTime, handleClick } = useSuccessPage();

    const buttons = React.useMemo(
        () => (
            <DialogButtonSberbox shouldFocusOnMount view={ButtonViews.primary} onClick={handleClick}>
                Очень жду
            </DialogButtonSberbox>
        ),
        [handleClick],
    );

    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <SuccessAnimation layout={layout} />
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPageSberbox buttons={buttons} decorations={decorations}>
            <DialogTitleSberbox>
                <MultilineText text={`Заказ принят. \nБудем у вас через \n${deliveryTime} минут`} />
            </DialogTitleSberbox>
            <DialogTextSberbox>
                <MultilineText text={'Чтобы узнать статус заказа, спросите \n«Какой статус заказа?»'} />
            </DialogTextSberbox>
        </DialogPageSberbox>
    );
};
