import React from 'react';
import { ButtonViews } from 'components/Button/Button';
import { MultilineText } from 'components/MultilineText/MultilineText';
import {
    DialogButtonPortal,
    DialogPagePortal,
    DialogTextPortal,
    DialogTitlePortal,
} from 'components/DialogPage/DialogPage@portal';
import { FullscreenDecoration } from 'components/FullscreenDecoration/FullscreenDecoration';
import { layout } from 'style/const/layout@portal';

import { useSuccessPage } from './Success.hooks';
import { SuccessAnimation } from './SuccessAnimation';

export const SuccessPortal: React.FC = () => {
    const { deliveryTime, handleClick } = useSuccessPage();

    const buttons = React.useMemo(
        () => (
            <DialogButtonPortal shouldFocusOnMount view={ButtonViews.primary} onClick={handleClick}>
                Очень жду
            </DialogButtonPortal>
        ),
        [handleClick],
    );

    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <SuccessAnimation layout={layout} />
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPagePortal buttons={buttons} decorations={decorations}>
            <DialogTitlePortal>
                <MultilineText text={`Заказ принят.\n Будем у вас через\n ${deliveryTime} минут`} />
            </DialogTitlePortal>
            <DialogTextPortal>
                <MultilineText text={'Чтобы узнать статус заказа,\n спросите «Какой статус\n заказа?»'} />
            </DialogTextPortal>
        </DialogPagePortal>
    );
};
