import React from 'react';
import styled from 'styled-components';
import { surfaceLiquid01, surfaceLiquid02 } from '@sberdevices/plasma-tokens';
import { Skeleton } from 'components/Skeleton/Skeleton';
import { layout } from 'style/const/layout@sberbox';

import { cardWidthPx, cardHeightPx } from '../ListItem/CatalogListItem';
import { cardGapPx } from '../List/CatalogList';

const columnCount = Math.floor((layout.width + cardGapPx) / (cardWidthPx + cardGapPx)) + 1;

const getWidthByCount = (count: number) => cardWidthPx * count + cardGapPx * (count - 1);

const skeletonWidth = getWidthByCount(columnCount);

const skeletonPaddingX = (layout.width - getWidthByCount(columnCount - 1)) / 2;
const skeletonPaddingY = (layout.contentHeight - cardHeightPx) / 2;

const backgroundColor = surfaceLiquid01;
const foregroundColor = surfaceLiquid02;

const StyledRoot = styled.div`
    box-sizing: border-box;
    height: ${layout.contentHeight}px;
    padding-left: ${skeletonPaddingX}px;
    padding-top: ${skeletonPaddingY}px;
`;

export const CatalogSkeleton: React.FC = () => (
    <StyledRoot>
        <Skeleton
            width={skeletonWidth}
            height={cardHeightPx}
            backgroundColor={backgroundColor}
            foregroundColor={foregroundColor}
            itemWidth={cardWidthPx}
            itemHeight={cardHeightPx}
            borderRadius={40}
            rowCount={1}
            columnCount={columnCount}
            paddingBetweenRows={cardGapPx}
            paddingBetweenColumns={cardGapPx}
            speed={2}
        />
    </StyledRoot>
);
