import React from 'react';
import styled from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { history } from 'tools/history';
import { preventScroll } from 'tools/scroll';
import { store } from 'store';
import { ROUTES } from 'store/constants/router';
import { AppLayoutSberbox } from 'components/AppLayout/AppLayout@sberbox';
import { Fonts } from 'components/Fonts/Fonts';
import { GlobalResetStyle } from 'components/GlobalResetStyle/GlobalResetStyle';
import { GlobalLayoutVars } from 'components/GlobalLayoutVars/GlobalLayoutVars';
import { Catalog } from 'pages/Catalog/Catalog';
import { Cart } from 'pages/Cart/Cart';
import { SuccessSberbox } from 'pages/Success/Success@sberbox';
import { NoAreaSberbox } from 'pages/NoArea/NoArea@sberbox';
import { PromoSberbox } from 'pages/Promo/Promo@sberbox';
import { AddressSimpleSberbox } from 'pages/AddressSimple/AddressSimple@sberbox';
import { AddressSimpleGuideSberbox } from 'pages/AddressSimpleGuide/AddressSimpleGuide@sberbox';
import { AddressSimpleQuestionSberbox } from 'pages/AddressSimpleQuestion/AddressSimpleQuestion@sberbox';
import { AddressSimpleInput } from 'pages/AddressSimpleInput/AddressSimpleInput';
import { AddressFullAllSberbox } from 'pages/AddressFullAll/AddressFullAll@sberbox';
import { AddressFullApartment } from 'pages/AddressFullApartment/AddressFullApartment';
import { AddressFullDoorCode } from 'pages/AddressFullDoorCode/AddressFullDoorCode';
import { AddressFullEntrance } from 'pages/AddressFullEntrance/AddressFullEntrance';
import { AddressFullFloor } from 'pages/AddressFullFloor/AddressFullFloor';
import { AddressFullPhone } from 'pages/AddressFullPhone/AddressFullPhone';
import { AddressFullAccommodationGuideSberbox } from 'pages/AddressFullAccommodationGuide/AddressFullAccomodationGuide@sberbox';
import { AddressFullPhoneGuideSberbox } from 'pages/AddressFullPhoneGuide/AddressFullPhoneGuide@sberbox';
import { Error } from 'pages/Error/Error';
import { WaitSberbox } from 'pages/Wait/Wait@sberbox';
import { StatusSummarySberbox } from 'pages/StatusSummary/StatusSummary@sberbox';
import { StatusDetails } from 'pages/StatusDetails/StatusDetails';
import { StatusReportSberbox } from 'pages/StatusReport/StatusReport@sberbox';
import { StatusEmptySberbox } from 'pages/StatusEmpty/StatusEmpty@sberbox';
import { layout } from 'style/const/layout@sberbox';

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: transparent;
`;

export const RootSberbox: React.FC = () => (
    <ReduxProvider store={store}>
        <ConnectedRouter history={history}>
            <GlobalResetStyle />
            <Fonts />
            <GlobalLayoutVars layout={layout} />
            <StyledContainer onScroll={preventScroll}>
                <AppLayoutSberbox>
                    <Switch>
                        <Route path={ROUTES.CATALOG}>
                            <Catalog />
                        </Route>
                        <Route path={ROUTES.CART}>
                            <Cart />
                        </Route>
                        <Route path={ROUTES.SUCCESS}>
                            <SuccessSberbox />
                        </Route>
                        <Route path={ROUTES.STATUS}>
                            <Switch>
                                <Route path={ROUTES.STATUS_SUMMARY}>
                                    <StatusSummarySberbox />
                                </Route>
                                <Route path={ROUTES.STATUS_DETAILS}>
                                    <StatusDetails />
                                </Route>
                                <Route path={ROUTES.STATUS_REPORT}>
                                    <StatusReportSberbox />
                                </Route>
                                <Route path={ROUTES.STATUS_EMPTY}>
                                    <StatusEmptySberbox />
                                </Route>
                            </Switch>
                        </Route>

                        <Route path={ROUTES.ADDRESS_SIMPLE}>
                            <AddressSimpleSberbox />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_GUIDE}>
                            <AddressSimpleGuideSberbox />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_QUESTION}>
                            <AddressSimpleQuestionSberbox />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_INPUT}>
                            <AddressSimpleInput />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL}>
                            <Switch>
                                <Route path={ROUTES.ADDRESS_FULL_ALL}>
                                    <AddressFullAllSberbox />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_PHONE}>
                                    <AddressFullPhone />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_APARTMENT}>
                                    <AddressFullApartment />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_ENTRANCE}>
                                    <AddressFullEntrance />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_FLOOR}>
                                    <AddressFullFloor />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_DOOR_CODE}>
                                    <AddressFullDoorCode />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_ACCOMMODATION_GUIDE}>
                                    <AddressFullAccommodationGuideSberbox />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_PHONE_GUIDE}>
                                    <AddressFullPhoneGuideSberbox />
                                </Route>
                            </Switch>
                        </Route>
                        <Route path={ROUTES.PROMO}>
                            <PromoSberbox />
                        </Route>
                        <Route path={ROUTES.NO_AREA}>
                            <NoAreaSberbox />
                        </Route>
                        <Route path={ROUTES.ERROR}>
                            <Error />
                        </Route>
                        <Route path={ROUTES.WAIT}>
                            <WaitSberbox />
                        </Route>
                    </Switch>
                </AppLayoutSberbox>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
