import React from 'react';
import styled from 'styled-components';
import { useContainerFocus } from 'hooks/useContainerFocus';
import { transition } from 'style/const/transition';
import { Layout } from 'style/const/layout';

import { HeaderAddress } from '../HeaderAddress/HeaderAddress';
import { HeaderCart } from '../HeaderCart/HeaderCart';

import { HeaderTitle } from './Title/HeaderTitle';
import { useHeaderAddressVisible, useHeaderCartVisible, useIsHeaderVisible } from './Header.hooks';

interface StyledHeaderProps {
    isVisible: boolean;
    headerPadding: string;
    layout: Layout;
}

export const StyledHeader = styled.div<StyledHeaderProps>`
    --spatial-navigation-contain: contain;

    position: relative;
    z-index: 3;

    height: ${({ layout }) => layout.headerHeight}px;

    box-sizing: border-box;
    padding: ${({ headerPadding }) => headerPadding};

    display: flex;
    justify-content: space-between;
    align-items: center;

    opacity: ${({ isVisible }) => Number(isVisible)};

    transition: opacity ${transition.default};
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const cartButtonRef = React.createRef<HTMLButtonElement>();

interface HeaderCommonProps {
    headerPadding: string;
    layout: Layout;
    renderBackButton(): React.ReactElement;
}

export const HeaderCommon: React.FC<HeaderCommonProps> = ({ headerPadding, layout, renderBackButton }) => {
    const isHeaderVisible = useIsHeaderVisible();
    const isAddressVisible = useHeaderAddressVisible();
    const isCartVisible = useHeaderCartVisible();

    const handleReceiveFocus = React.useCallback(() => {
        if (isCartVisible) {
            cartButtonRef.current?.focus();
        }
    }, [isCartVisible]);

    const { handleFocus, handleBlur } = useContainerFocus({
        onReceiveFocus: handleReceiveFocus,
    });

    const backButton = React.useMemo(() => renderBackButton(), [renderBackButton]);

    return (
        <StyledHeader
            headerPadding={headerPadding}
            isVisible={isHeaderVisible}
            layout={layout}
            onFocus={handleFocus}
            onBlur={handleBlur}
        >
            {backButton}
            <StyledSection>
                <HeaderTitle />
            </StyledSection>
            <StyledSection>
                <HeaderAddress isVisible={isAddressVisible} />
                <HeaderCart forwardRef={cartButtonRef} isVisible={isCartVisible} />
            </StyledSection>
        </StyledHeader>
    );
};
