import React from 'react';
import styled from 'styled-components';

import { HeaderBackButtonCommon, StyledBackButtonCommon } from './HeaderBackButton';

const StyledBackButton = styled(StyledBackButtonCommon)`
    top: 50px;
`;

export const HeaderBackButtonPortal: React.FC = () => {
    const renderBackButton = React.useCallback(
        (children: React.ReactElement) => <StyledBackButton>{children}</StyledBackButton>,
        [],
    );

    return <HeaderBackButtonCommon renderBackButton={renderBackButton} />;
};
