import React from 'react';
import styled from 'styled-components';
import { icon } from 'style/const/icon';

import { useIsBackButtonVisible } from '../Header.hooks';
import { ReactComponent as ChevronLeftSvg } from '../Header.assets/chevron-left.svg';

interface BackButtonProps {
    renderBackButton(children: React.ReactElement): React.ReactElement;
}

const backButtonIconSizePx = icon.m.sizePx;

const StyledChevronLeft = styled(ChevronLeftSvg)`
    width: ${backButtonIconSizePx}px;
    height: ${backButtonIconSizePx}px;
`;

export const StyledBackButtonCommon = styled.span`
    position: absolute;
    left: 24px;

    width: 80px;
    height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;
`;

export const HeaderBackButtonCommon: React.FC<BackButtonProps> = ({ renderBackButton }) => {
    const isBackButtonVisible = useIsBackButtonVisible();

    const backButton = React.useMemo(() => renderBackButton(<StyledChevronLeft />), [renderBackButton]);

    if (!isBackButtonVisible) {
        return null;
    }

    return backButton;
};
